.navbar-toggler {
  background-color: transparent !important;
  border-color: transparent !important;
}

.navbar-toggler-icon {
  background-color: transparent;
  border: none;
  position: relative;
  width: 24px;
  height: 24px;
}

.navbar-toggler-icon::before,
.navbar-toggler-icon::after,
.navbar-toggler-icon div {
  content: '';
  display: block;
  width: 24px;
  height: 3px;
  background-color: #9381ff; /* Custom color for the lines */
  position: absolute;
  left: 0;
  transition: all 0.2s;
}

.navbar-toggler-icon::before {
  top: 2px;
}

.navbar-toggler-icon div {
  top: 1px;
}

.navbar-toggler-icon::after {
  top: 10px;
}

.container-flex {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  text-align: center;
  display: flex;
  flex-direction: column;
  align-items: center;
