}

.tooltip-text {
  color: var(--scoll-color);
  text-decoration: underline;
  background-color: white;
  cursor: pointer;
}

.close-tooltip{
  display: none;
}

.tooltip-arrow {
  z-index: 1;
  position: absolute;
  height: 15px;
  width: 15px;
  background-color: #000;
  margin-left: 2em;
  transform: rotate(-45deg) translate(-1.5em, -1em);
}

.external-link-icon{
  font-size: 0.8em;
  align-items: center;
}

.tooltip-container {
  z-index: 1;
  text-shadow: 0 !important;
  text-shadow: 2px 10px 10px #000;
  font-style: normal;
  position: absolute;
  background-color: #000;
  text-align: left;
  align-items: center;
  padding: 1em 1em 1em 1em;
  border-radius: 10px;
  margin-left: 0.5em;
  box-shadow: 0px -25px 20px -20px rgba(0, 0, 0, 0.45), 25px 0 20px -20px rgba(0, 0, 0, 0.45), 0px 25px 20px -20px rgba(0, 0, 0, 0.45), -25px 0 20px -20px rgba(0, 0, 0, 0.45);
  transform: translate(0, -3em);
}

.opportunity-div {
  display: flex;
  justify-content: center;
  font-size: 1em;
  text-align: center;
  font-weight: bold;
  color: #000000;
  margin: 1em 1em;
}

.lead {
  padding: 0;
  color: #000000;
  text-shadow: 2px 5px 25px rgb(187, 188, 188);
  text-align: center;
  align-items: center;
  font-size: 1em;
  font-weight: bold;
}

.head-name {
  background: transparent;
  text-shadow: 2px 10px 10px rgb(187, 188, 188);
  color: var(--scoll-color);
  -webkit-animation: myfirst 5s 1;
          animation: myfirst 5s 1;
  letter-spacing: 30px;
  font-weight: 900;
  font-size: 3em;
}

.welcome {
  text-align: center;
  align-items: center;
  text-shadow: 2px 10px 10px rgb(187, 188, 188);
  color: #000000;
}

.subContainer-flex {
  display: flex;
  justify-content: center;
  flex-direction: row;
  margin-left: 15em;
  margin-right: 15em;
  text-align: center;
  align-items: center;
}

.social-icon {
  text-align: center;
  margin: 0;
  font-size: 24px;
  display: flex;
  justify-content: center;
  flex-direction: row;
}

.scroll-down-div {
  display: flex;
  justify-content: center;
}

.scroll-down-container {
  height: 45px;
  width: 45px;
  margin-top: 1em;
  border-radius: 60px;
  text-align: center;
  -webkit-animation: scrollDwon 1.5s infinite;
          animation: scrollDwon 1.5s infinite;
}

.scroll-down-action {
  width: 25px;
  height: 25px;
  border-left: 5px double var(--scoll-color);
  border-bottom: 5px double var(--scoll-color);
  transform: rotate(-45deg) translate(50%);
}

@-webkit-keyframes scrollDwon {
  0% {
    transform: translate(0);
  }

  20% {
    transform: translateY(15px);
  }

  40% {
    transform: translate(0);
  }
}

@keyframes scrollDwon {
  0% {
    transform: translate(0);
  }

  20% {
    transform: translateY(15px);
  }

  40% {
    transform: translate(0);
  }
}

@-webkit-keyframes myfirst {
  0% {
    transform: scaleX(0.9);
  }
}

@keyframes myfirst {
  0% {
    transform: scaleX(0.9);
  }
}

.about-link {
  color: var(--scoll-color) !important;
  text-decoration: underline;
}

.about-link:hover {
