.timeline-box {
    background-color: #ffffff;
    margin-top: 8em;
}

.timeline-container {
    overflow-x: scroll;
    scrollbar-width: thin;
    scrollbar-color: transparent #ffffff;
    scroll-behavior: smooth;
}

.timeline {
    display: flex;
    flex-direction: row;
    overflow-x: scroll;
    scroll-behavior: smooth;
}

.continue-line {
    width: 15em;
    margin-top: 25px;
    border: 2px solid var(--scoll-color);
    height: 1px;
}

.timeline-content {
    display: flex;
    flex-direction: column;
    transition: transform 0.3s, box-shadow 0.3s;
    overflow-x: auto;
    scrollbar-width: thin;
    scrollbar-color: var(--scroll-color) #ffffff;
    scroll-behavior: smooth;
    white-space: nowrap;
    padding-bottom: 20px;
    background-color: #fff;
    background-color: white;
}

.timeline-text {
    margin-top: 1.5em;
    display: flex;
}

.timeline-content::-webkit-scrollbar {
    width: 6px;
    height: 6px;
}

.timeline-content::-webkit-scrollbar-track {
    background: #ffffff;
}

.timeline-content::-webkit-scrollbar-thumb {
    background-color: var(--scroll-color);
    border-radius: 3px;
}

.timeline-content::-webkit-scrollbar-thumb:hover {
    background-color: #888;
}

.timeline-event {
    height: 12px;
    width: 12px;
    border: 2px solid black;
    border-radius: 100%;
    background-color: black;
}

.next-line {
    margin-top: 25px;
    width: 15em;
    border: 2px solid var(--scoll-color);
    height: 1px;
}

.event-description {
    display: flex;
    flex-direction: column;
    text-align: center;
}

.time-description-container {
    padding: 6px;
    background-color: black;
    border-radius: 10px;
    border: 1px solid var(--appstartColor);
    margin: 0 3em 0 3em;
}


.time-description-container p {
    margin-bottom: 0.5em;
}

.time-description {
    background-color: white;
    padding: 3%;
    color: var(--accentColor);
    font-size: 16px;
    border-radius: 10px;
    width: 100%;
    display: flex;
    flex-direction: column;
    letter-spacing: 1px;
    font-weight: bolder;
}

.timeline-extra-text {
    font-size: 12px;
    text-shadow: 1px 1px 1px rgb(187, 188, 188);
    letter-spacing: 0;
    font-weight: 100;
}

.title-description {
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.under-line {
    display: flex;
    flex-direction: row;
}

.timeline-buttons {
    display: flex;
    flex-direction: row;
    justify-content: center;
    margin: 5em 0 10em 0;
}

.timeline-connection {
    border-left: 3px solid var(--scoll-color);
    height: 20px;
    margin-left: 5px;
}

.event-icon {
    font-size: 30px !important;
    margin-bottom: 2%;

}

.event-date {
    font-size: 18px;
    letter-spacing: 1px;
    color: black;
    font-weight: bolder;
}

.timeline-button {
    margin: 0 2em 1em 2em;
    font-size: 28px;
    color: var(--accentColor);
    cursor: pointer;
    text-shadow: 1px 10px 10px rgb(187, 188, 188);
}

.timeline-button:hover {
    margin: 0 2em 0 2em;
    font-size: 28px;
    cursor: pointer;
    text-shadow: 1px 1px 1px rgb(187, 188, 188);
}

.instruction-text {
    margin-top: 1em;
    margin-left: 0.5em;
    height: 20px;
    color: black;
}

.nextButton {
    margin-left: 5px;
    -webkit-animation: goNext 1s infinite;
            animation: goNext 1s infinite;
    text-align: center;
    align-items: center;
}

@-webkit-keyframes goNext {
    0% {
        /* left: 50; */
        transform: translateX(5px);
    }


    100% {
        /* left: 0 */
        transform: translateX(0);
    }
}

@keyframes goNext {
    0% {
        /* left: 50; */
        transform: translateX(5px);
    }


    100% {
        /* left: 0 */
        transform: translateX(0);
    }
}

.time-description {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    background-color: #f9f9f9;
    border: 1px solid #ddd;
